import { Input, Typography } from '@mui/material';
import { Box } from '@mui/system';
import Sheet from 'react-modal-sheet';

import type { VerifyOTPProps } from './types';

function VerifyOTP({ isOpen, onClose }: VerifyOTPProps) {
    return (
        <Sheet isOpen={isOpen} onClose={onClose} snapPoints={[0.5]} initialSnap={0}>
            <Sheet.Container>
                <Sheet.Content>
                    <Typography>
                        <Box>
                            Verify OTP
                        </Box>
                        <Input
                            inputProps={{
                                maxLength: 10,
                                inputMode: 'numeric', pattern: '[0-9]*'
                            }}
                            fullWidth
                            placeholder="097xxxxxx"
                            disableUnderline={true}
                            autoFocus
                            type="tel"
                            sx={{ fontSize: 24, color: '#33CB33', fontWeight: 'bold' }}
                        // value={phone}
                        // onChange={(e) => setPhone(e?.target?.value)}
                        />
                    </Typography>
                </Sheet.Content>
            </Sheet.Container>

            <Sheet.Backdrop />
        </Sheet>
    )
}

export default VerifyOTP