import { Input } from '@mui/material';
import Typography from '@mui/material/Typography';
import LoadingButton from '@mui/lab/LoadingButton';
import { Box } from '@mui/system';

import Meta from 'components/Meta';
import { FullSizeFlexBox } from 'components/styled';
import { useEffect, useState } from 'react';
import VerifyOTP from 'components/VerifyOTP';
// import BottomSheet from 'components/BottomSheet';

// import { useNavigate } from 'react-router-dom';

function Login() {
  // const navigate = useNavigate()
  const [phone, setPhone] = useState<string>('')
  const [isActiveButton, setIsActiveButton] = useState<boolean>(false)
  const [loading, setLoading] = useState<boolean>(false)
  const [isOpenBottomSheet, setIsOpenBottomSheet] = useState<boolean>(false)

  useEffect(() => {
    if (phone) {
      setIsActiveButton(true)
    } else {
      setIsActiveButton(false)
    }
  }, [phone])

  const onSubmit = () => {
    setLoading(true)
    setTimeout(() => {
      setLoading(false)
      setIsOpenBottomSheet(true)
    }, 2000)
  }


  return (
    <>
      <Meta title="Login" />
      <FullSizeFlexBox display="flex" flexDirection="column" justifyContent="space-between">
        <Box sx={{ paddingTop: 5 }}>
          <Typography>
            <Box sx={{ color: '#33CB33', fontSize: 24, fontWeight: 'bold' }}>Cho bui ti,</Box>
          </Typography>
          <Typography>
            <Box sx={{ fontSize: 24, fontWeight: 'bold' }}> S in thoi ca bn l</Box>
          </Typography>
          <Input
            inputProps={{
              maxLength: 10,
              inputMode: 'numeric', pattern: '[0-9]*'
            }}
            fullWidth
            placeholder="097xxxxxx"
            disableUnderline={true}
            autoFocus
            type="tel"
            sx={{ fontSize: 24, color: '#33CB33', fontWeight: 'bold' }}
            value={phone}
            onChange={(e) => setPhone(e?.target?.value)}
          />
        </Box>

        <LoadingButton disabled={!isActiveButton} loading={loading} loadingIndicator="Xin i..." variant="contained" disableElevation onClick={onSubmit}>Tip tc</LoadingButton>
      </FullSizeFlexBox>
      <VerifyOTP isOpen={isOpenBottomSheet} onClose={() => setIsOpenBottomSheet(false)} />
    </>
  );
}

export default Login;
